@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://fonts.googleapis.com/css?family=VT323");

.App {
  text-align: center;
  font-family: "Press Start 2P", "VT323";
  color: black;
}

* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

.overlay {
  opacity: 0.85;
  width: 100%;
  min-height: 100vh;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: fixed;
}

.nav-bar{
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: black;
}

.main-mint{
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: black;
}

.moving-background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-image: url("./assets/background/back.jpg"); */
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(232,241,255,1) 0%, rgba(0,78,255,0.8015581232492998) 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 40% 40%;
}

.c-button{
  background-color: #008fd4;
  border-radius: 5px;
  box-shadow: 0px 2px 2px 1px #0F0F0F;
  color: white;
  cursor: pointer;
  font-family: inherit;
  padding: 15px;
  margin: 10;
}

.styledSelect {
  width: 20;
  height: 200;
  border-radius: 10px;
  right: 6px;
  font-family:inherit;
  width: 700;
  height: 40px;
  text-align: center;
  padding-left:30px;
  margin-top: 10px;
}

.c-address-input{
  width:700px;
  height:40px;
  text-align:center;
  padding-left: 30px;
  margin-top:10px; 
  font-family:inherit;

}

.c-amount-input{
  font-family:inherit;

  width:200px;
  height:40px;
  text-align:center;
  padding-left: 30px;
  margin-top:10px; 
}